	module.exports = {"data":{"markdownRemark":{"html":"<p> <strong>WHEREAS</strong> </p>\n<ul>\n<li>\n<p>Profit shifting tax avoidance schemes, such as the use of tax havens, result in:oErosion of the national tax baseoIncreasing government deficits</p>\n</li>\n<li>\n<p>A concentration of wealth and power;</p>\n</li>\n<li>\n<p>Redressing tax avoidance involves resolving the problems of:o competition among countries for corporate investmento profit shifting to tax havens;</p>\n</li>\n<li>\n<p>According to the organization Canadians for Tax Fairness, Canadian tax losses due to taxhavens range from $10 to $15 billion a year;</p>\n</li>\n<li>\n<p>Tax Justice Network estimates that globally governments are losing tax revenues of:o$500bn a year due to profit shifting tax avoidance by corporationso$200bn a year due to undeclared offshore wealth of individuals;</p>\n</li>\n<li>\n<p>Mechanisms for moving corrupt money, criminal money and commercial tax-avoiding money are the same;</p>\n</li>\n<li>\n<p>Concerted international action is essential to redressing tax avoidance;</p>\n</li>\n</ul>\n<p><strong>BE IT RESOLVED</strong> the Liberal Party of Canada urge the Government of Canada to redress tax avoidance by working internationally to eliminate tax avoidance strategies through such measures as:</p>\n<ul>\n<li>\n<p>Public registries of the actual (beneficial) owners of companies, trusts, etc..</p>\n</li>\n<li>\n<p>Public country-by-country reporting requirements for all multinationals, in order to tax profits in the countries where they originated</p>\n</li>\n<li>\n<p>Establishing an intergovernmental body at the United Nations to negotiate a globalagreement to eliminate tax havens and redress tax avoidance.(Women’s Commission)</p>\n</li>\n</ul>","frontmatter":{"path":"/policy/redressing-tax-avoidance","date":"Mon Mar 05 2018 23:27:04 GMT-0500 (EST)","title":"Redressing Tax Avoidance"}}},"pathContext":{}}

